import { createRouter, createWebHistory } from 'vue-router';

import LandingPage from '../pages/landing/index.vue';
import MainPage from '../pages/main/index.vue';

// This should be managed by the Store in the near future
// This should be defined here, because both beforeEnter in routes const and beforeEach use it
const isLoggedIn = localStorage.getItem('token');

const routes = [
	{
		path: '/',
		component: LandingPage,
		meta: { isAuthRequired: false },
		beforeEnter: (to, from, next) => {
			if (to.path === '/' && to.query.token) {
				console.log(to.query.token);
				localStorage.setItem('token', to.query.token);
				return next('/main');
			}

			if (to.path === '/' && isLoggedIn) {
				return next('/main');
			}
		},
	},
	{ path: '/main', component: MainPage, meta: { isAuthRequired: true } },
];

const router = createRouter({
	history: createWebHistory(),
	routes,
});

router.beforeEach((to, from, next) => {
	const isAuthRequired = to.matched.some(
		(record) => record.meta.isAuthRequired
	);

	if (isAuthRequired && !isLoggedIn) {
		return next('/signin');
	}

	next();
});

export default router;
